.navBar {
  background-color: #2e3f6e31;
  color: white;
  height: 77px;
  width: 100%;

  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 1.4rem;
  position: fixed;
  top: 0px;
  z-index: 10;
}

.navBar__list {
  display: -webkit-flex;
  display: flex;
  height: 100%;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  -webkit-align-items: center;
          align-items: center;
  position: relative;
  top: 0;
  z-index: -1;
  padding-right: 20px;
}

.navBar__list-li {
  list-style: none;
  height: 77px;
  width: 120px;
  position: relative;
  text-align: center;
}

.navBar__list-li a {
  width: 100%;
  height: 100%;
  display: block;
  cursor: pointer;
}

.navBar__Label {
  position: relative;
  top: calc(50% - 15px);

  cursor: pointer;
}

.navBar__list-li:hover {
  color: rgb(255, 255, 255);
  background-color: #1d336e93;
  border-radius: 10px;
  border: solid 1px black;
}

.button-menu {
  height: 35px;
  width: 35px;

  position: relative;
  float: right;
  display: none;

  top: 20px;
  right: 20px;

  background-image: url(data:image/png;base64,);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  cursor: pointer;
}

.button-up {
  height: 50px;
  width: 50px;

  margin-left: 10px;
  margin-top: 12px;

  position: relative;
  float: left;

  background-image: url(data:image/png;base64,);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  cursor: pointer;
}

.button-up a {
  height: 50px;
  width: 50px;
  display: block;
}

@media screen and (max-width: 1024px) {
  .navBar .navBar__list {
    display: none;
  }

  .button-menu {
    display: inline;
    display: initial;
  }

  .navBar__list-clicked {
    position: relative;

    border-radius: 0px;
    width: 100vw;
    height: 100vh;
    float: right;
    background-color: #2f2d2adf;
  }

  .navBar__list-clicked li {
    width: 100%;
  }
  .navBar__list-clicked li a {
