.showModal {
  opacity: 1;
  z-index: 2;
  -webkit-animation: show 0.8s;
          animation: show 0.8s;

  -webkit-transform: scale(1);

          transform: scale(1);
}
@-webkit-keyframes show {
  from {
    -webkit-transform: scale(0);
            transform: scale(0);
    opacity: 0;
    z-index: -1;
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
    z-index: 2;
  }
}
